import { lazy, Suspense } from 'react';
import { useDispatch, useSelector } from 'react-redux';

import './empresas.css';
import { startEnterpriseSelect } from '../../actions/auth';
const EmpresasCard = lazy( () => import('./EmpresasCard') );

const EmpresasScreen = () => {

    const { enterprises } = useSelector(state => state.auth);
    const { cardsEnterprises }  = useSelector( state => state.ui );
    const dispatch = useDispatch();

    if(!enterprises)
        dispatch( startEnterpriseSelect() );

    return (
        // <div className="container-empresas-card">
        <div 
            className={cardsEnterprises ? 'container-empresas-card-hidden' : 'container-empresas-card' }
        >
            <Suspense fallback={
                <div className='container-loading'>
                    <i className="fa fa-circle-o-notch fa-spin fa-3x loading"></i>
                </div> 
            }>
                {
                    enterprises 
                        ? enterprises.map( enterprise => 
                                <EmpresasCard 
                                    key={ enterprise.codigo }
                                    { ...enterprise } 
                                />
                            )
                        : ''
                }
            </Suspense>
        </div>
    )
}

export default EmpresasScreen;